<template><section><html><head></head><body><h1>VcNavigationSM</h1>
<p>The <code v-pre="">vc-navigation-sm</code> component is used to load the navigation compass widget of SuperMap style. <strong>Note:</strong> This component requires <code v-pre="">import 'vue-cesium/lib/vc-navigation-sm.css'</code>, Requires <code v-pre="">css-loader</code>.</p>
<h2>Example</h2>
<h3>Load a VcNavigation</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-navigation-sm ref="navigation" :options="options"></vc-navigation-sm>
      </vc-viewer>
      <div class="demo-tool">
        <span>EnableCompass</span>
        <md-switch v-model="options.enableCompass"></md-switch>
        <span>EnableZoomControl</span>
        <md-switch v-model="options.enableZoomControl"></md-switch>
        <span>EnableCompassOuterRing</span>
        <md-switch v-model="options.enableCompassOuterRing"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
