<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>enableCompass</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> option used to enable or disable the compass.</td>
</tr>
<tr>
<td>enableZoomControl</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> option used to enable or disable the zoom control.</td>
</tr>
<tr>
<td>enableCompassOuterRing</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> option used to enable or disable the Compass Outer Ring.</td>
</tr>
</tbody>
</table>
<hr>
<h2>Event</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>Triggers when PolylineGraphics is ready. It returns a core class of Cesium, a viewer instance.</td>
